import { Authentication } from '@siag/js-egov-authentication';

// @ngInject
const handleResponses = (response) => {
    Authentication.handleAjaxResponse({
        url: response.config.url,
        status: response.status,
        getResponseHeader: function (headerName) {
            return response.headers(headerName);
        },
        getResponseBody: function () {
            // Example, catch 401 errors
            if (response.status === 403) {
                return response.data;
            }
        }
    });
};

export function delegationChangeHttpInterceptor($q) {
    return {
        request: function (config) {
            // pass in proper `setRequestHeader(..)` function
            // as the native $http doesn't have one
            if (Authentication.getAuthSession()) {
                Authentication.getAuthSession().handleAjaxRequest({
                    setRequestHeader: function (headerName, headerValue) {

                        // set it on the Angular $http object
                        config.headers[headerName] = headerValue;
                    }
                });
            }

            return config;
        },
        response: function (response) {
            handleResponses(response);
            return response;
        },
        responseError: function (response) {
            handleResponses(response);
            return $q.reject(response);
        }
    }
}



