import * as angular from 'angular';

import { userwidgetComponent } from './src/widget.component';
import { delegationChangeHttpInterceptor  } from './src/delegationChangeHttpInterceptor';

// export * from '@siag/jsui-egov-authentication';

export const EgovauthModule =
    angular.module('egov.userwidget', [])
        .directive('egovUserwidget', userwidgetComponent)
        .factory('delegationChangeHttpInterceptor', delegationChangeHttpInterceptor)
        .config(['$httpProvider', function($httpProvider) {
            $httpProvider.interceptors.push('delegationChangeHttpInterceptor');
        }]);



