
import { EgovUserwidget } from '@siag/jsui-egov-authentication';

export function userwidgetComponent() {
    return {
        restrict: 'A',
        scope: {},
        bindToController: {
            user: '=', // one-way directional binding
            config: '='
            // language: '=?',
            // loginUrl: '=?',
            // myProfileUrl: '=?',
            // logoutUrl: '=?',
            // newDelegationUrl: '=?',
            // allowDelegationChange: '=?',
            // delegations: '=?',
            // currentDelegationId: '=?',
            // changeDelegation: '&?',
            // userChange: '&?',
        },
        // @ngInject
        controller: function ($element) {
            var vm = this;
            vm.$onInit = onInit;

            function onInit() {
                console.log('widget onInit');
                const userwidget = new EgovUserwidget($element[0], vm.config);

                if (vm.user) {
                    userwidget.render({ user: vm.user });
                } else {
                    userwidget.render();
                }
            }
        },
        controllerAs: '$ctrl'
    }
}




